import { csvParse, autoType } from 'd3-dsv';
import { bbox } from '@turf/turf';
import { parse } from './wellknown.js';

export async function getData(url) {
  let response = await fetch(url);
  let string = await response.text();
	let data = await csvParse(string, autoType);
  return data;
}

export function getSum(data) {
	let sum = 0;
	let ages = {};
	data.forEach(d => {
		let value = Number(d.value);
		sum += value;
	});
	return sum;
}

export function procData(data, sum) {
	let ages = {};
	data.forEach(d => {
		let value = Number(d.value);
		d.value = value;
		if (!ages[d.age]) {
			ages[d.age] = value;
		} else {
			ages[d.age] += value;
		}
	});
	data.forEach((d, i) => {
		let value = Number(d.value);
		data[i].width = (value / ages[d.age]) * 100;
		data[i].height = (ages[d.age] / sum) * 100;
	});
	return data;
}

export async function getPlace(code) {
	let apiurl = 'https://staging.gss-data.org.uk/sparql?query=';
	let query = `PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
PREFIX qb: <http://purl.org/linked-data/cube#>
PREFIX code: <http://purl.org/linked-data/sdmx/2009/code#>
PREFIX skos: <http://www.w3.org/2004/02/skos/core#>
PREFIX this: <http://gss-data.org.uk/data/gss_data/towns-high-streets/ons-population-estimates-by-output-areas-electoral-health-and-other-geographies-england-and-wales#>
PREFIX measure: <http://gss-data.org.uk/def/measure/>
PREFIX dimension: <http://purl.org/linked-data/sdmx/2009/dimension#>
PREFIX level: <http://gss-data.org.uk/def/geography/level/>
PREFIX agecat: <http://gss-data.org.uk/def/concept/labour-market-age-categories/>
SELECT ?sex ?age (SUM(?count) AS ?value)
WHERE {
  VALUES ?oa { level:E00 level:W00 }
  VALUES ?ages { agecat:Y0T15 agecat:Y16T24 agecat:Y25T49 agecat:Y50T64 agecat:Y_GE65 }
  VALUES ?sexes { code:sex-F code:sex-M }
  {
    SELECT DISTINCT ?child WHERE {
      BIND (<http://statistics.data.gov.uk/id/statistical-geography/${code}> as ?geo )
      ?geo skos:narrower+ ?child .
      ?child a ?oa .
    }
  }    
  ?obs a qb:Observation ;
             qb:dataSet this:dataset ;
             qb:measureType measure:count ;
             dimension:refArea ?child ;
             measure:count ?count ;
             dimension:sex ?sexes ;
             dimension:age ?ages .
  ?sexes skos:prefLabel ?sex .
  ?ages rdfs:label ?age .
}
GROUP BY ?geo ?sex ?age
LIMIT 10`;
	let response = await fetch(apiurl + encodeURIComponent(query));
  let string = await response.text();
	let data = await csvParse(string);
  return data;
}

// Function to get boundary polygon based on place code
export async function getBoundary(code) {
	let apiurl = 'https://pmd3-production-drafter-onsgeo.publishmydata.com/v1/sparql/live?query=';
	let query = `PREFIX geosparql: <http://www.opengis.net/ont/geosparql#>
SELECT ?geometry
WHERE {
  <http://statistics.data.gov.uk/id/statistical-geography/${code}/geometry> geosparql:asWKT ?geometry
}
LIMIT 1`;
  let response = await fetch(apiurl + encodeURIComponent(query));
  let string = await response.text();
	let data = await csvParse(string);
	
	// Convert polygon from WKT to geojson format
	let geojson = await parse(data[0].geometry);
	
	// Get the lon/lat bounding box of the polygon
	let bounds = await bbox(geojson);
	
  return {
		geometry: geojson,
		bounds: bounds
	};
}